import { Link } from "react-router-dom";
import CheckBox from "./Checkbox";
import "./Navbar.css"
function  Navbar(){
    function shownavbar(){
        let nav=document.getElementById("nav");
        if(nav.style.display===""){
            nav.style.display="flex";
            nav.parentElement.style.backgroundColor="var(--primary-nav)"
        }
        else{
            nav.style.display="";
            nav.parentElement.style.backgroundColor="transparent"
        }
    }
    return(
        <div className="navbar">
            <img id="btn-nav" className="btn toggle" onClick={shownavbar} alt="" />             
            <div id="nav" className="nav-link-container">
                <Link to="/" className="btn">Home</Link>
                <Link to="/projects" className="btn projects-link">Projects</Link>    
                <Link to="/blog" className="btn blog-link">Blog</Link>
                <Link to="/skills" className="btn skills-link">Skills</Link>
                <Link to="/About" className="btn About-link">About</Link>
                <CheckBox></CheckBox>
            </div>
        </div>
    )
}
export default Navbar;