import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BrowserRouter as Router } from 'react-router-dom';
import Navbar from "./Navbar"
ReactDOM.render(
  <React.StrictMode>
    <Router>
    <Navbar></Navbar>
      <App />  
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);