import { useEffect, useState } from "react";
import ExpandedProject from "./ExpandedProject"
function Project(props){
    let title = props.title;
    let img= "https://raw.githubusercontent.com/xandrf/"+title+"/master/favicon.svg";
    let project = props.project;
    let link = project.html_url;
    let desclink="https://raw.githubusercontent.com/xandrf/"+title+"/master/readme.md";
    let [desc,setDesc] = useState(true);
    let [hasDesc,setHasDesc] = useState(false);
    let [imageloaded,setImageLoaded] =useState(false);
    let [isExpanded,setIsExpanded]=useState(false);
    let onImageError = () => {
        setImageLoaded(true)
    }
    function getRepdesc(){
        fetch(desclink).then(response=>response.text()).then(data=>{setDesc(data);setHasDesc(true);}).catch(err=>{
            console.log(err)
            setDesc("We Werent able to find the data")
            setHasDesc(true);
        }
            );
    }

    useEffect(()=>getRepdesc());
    function changeIsExpanded(){
        return isExpanded?setIsExpanded(false):setIsExpanded(true);
    }
    return(
       
       <div className="project">
           {isExpanded && <ExpandedProject onClick={changeIsExpanded } 
            link={link}
            image={imageloaded ? "https://upload.wikimedia.org/wikipedia/commons/d/d1/Gnome-image-svg-wip.svg":img} 
            title={title}
            desc={desc}>
                </ExpandedProject>}    
           <img onError={onImageError} src={imageloaded ? "https://upload.wikimedia.org/wikipedia/commons/d/d1/Gnome-image-svg-wip.svg":img} alt="cant load" className="content-image"/>
           <span className="title">{title}</span>
           {!hasDesc && <div className="content-text">Loading</div> }
           {hasDesc && desc!==undefined && <div className="content-text">{desc}</div> }
           
           <button className="btn link-container"rel="noreferrer" onClick={changeIsExpanded}>
               <div className="btn btn-secondary"> 
                    See Project
                </div>
            </button>
       </div>
    )
}
export default Project;