import { useEffect, useState } from "react";
import "./ExpandedProject.css"
export default function ExpandedProject(props){
    return(
        <div>
           
            <div class="expanded-project-container">
                <div class="expanded-project">
                <img src={props.image} alt="cant load" className="content-image"/>
                <div className="title">{props.title}</div>
                    <p>{props.desc}</p>
                    <a  href={props.link} target="_blank" className="btn link-container"rel="noreferrer">
                    
                        
                        <button className="btn btn-secondary"> 
                            See Project
                        </button>
                    </a>
                </div>
            </div>
             <div class="background-close"onClick={props.onClick}>
            
            </div>
        </div>   
    )
}
