import { useState } from "react"

export default function CheckBox(){
    const [ischecked, setIsChecked] = useState(false); 
    function changeTheme(){

        var r = document.querySelector(':root');
        var nav_button= document.querySelector("#btn-nav");
        if(ischecked){
            // Set the value of variable --blue to another value (in this case "lightblue")
            r.style.setProperty('--primary', 'rgba(0, 0, 0, 0.945)');
            r.style.setProperty('--secondary', 'white');
            nav_button.style.setProperty("filter","invert(1)")
            setIsChecked(false);
        }
        else{
            r.style.setProperty('--primary', 'white');
            r.style.setProperty('--secondary', 'black');

            nav_button.style.setProperty("filter","none")

            setIsChecked(true)
        }
    }
    return (
    <label className="checkbox-container ">
        <input type="checkbox"/>
        <span className="check" checked={ischecked} onClick={changeTheme}></span>
    </label>
    )
}