import './App.css';
import ProjectContainer from './ProjectsContainer';
import { Switch,Route} from 'react-router-dom';
import Home from "./Home"
import Skills from "./Skills"
import About from './About';

function App() {
  return (
    <div className="App">
      <Switch>
      <Route exact path="/">
          <Home/>
        </Route>
        <Route path="/projects">
          <ProjectContainer />
        </Route>
        <Route path="/skills">
          <Skills></Skills>
        </Route>
        <Route path="/About"><About /></Route>
      </Switch>
    </div>
  );
}

export default App;
